<template>
<section class="wrap has_header_wrap c_wrap purchase_wrap">
	<section class='wrap_in' :class="{ unscrollable: isUnscRollable }">
		<!-- 头部标题 -->
		<mt-header fixed title="订单确认">
	      <mt-button icon="back" slot="left" @click.prevent="goBack"></mt-button>
		</mt-header>
		<!-- 头部标题 end -->
		
		<div class="purchase_info">
			<div class="block">
				<div class="address" v-if='showAddress' @click="jumpAddress">
					<h3><span>收货人：</span><span>{{ defaultAddList.contactMan }}</span></h3>
					<p>收货地址：<!-- {{ defaultAddList.city }}{{ defaultAddList.county }} --></p>
					<p>{{ defaultAddList.address }}</p>
				</div>
				<!-- 没有收货地址时显示 -->
				<router-link v-if='!showAddress' to="myinfo/address?form=purch" class="add_address_btn">
					<i class="icons_add"></i>
					<span class="inline_block">添加收货地址</span>		
				</router-link>
				<!-- 没有收货地址时显示 end -->
			</div>
			<div class="block lists">
				<div class="item d_flex">
					<div class="i_img">
						<img v-lazy="productData.photodURL" :alt="productData.productName">
					</div>
					<div class="i_info">
						<p class="d_flex"><span>{{ productData.productName }}</span><span>{{ productData.ourPrice?'￥'+productData.ourPrice:'' }}</span></p>
						<p class="d_flex"><span>{{ params.specName?params.specName:productData.spec }}</span><span>{{ 'x'+params.num }}</span></p>
					</div>
				</div>
				<!-- <div class="item d_flex">
					<div class="i_img">
						<img src="../../../static/images/proimg.jpg" alt="">
					</div>
					<div class="i_info">
						<p class="d_flex"><span>赛典 单唾液酸四己糖神经节苷脂钠注射液</span><span>￥320.00</span></p>
						<p class="d_flex"><span>2ml:20mg*6支/盒</span><span>×1</span></p>
					</div>
				</div> -->
			</div>
			<!-- 配送方式 -->
			<div class="block" @click="chooseSendWay">
				<mt-cell title="配送方式" is-link></mt-cell>
				<!-- 选择配送方式后显示 -->
				<mt-cell class="isSendWay">
					<span slot="title"><span>{{ defaultExp.logisticsName }}</span><span>运费{{ defaultExp.byWeight | chargeFilter }}</span></span>
				</mt-cell>
				<!-- 选择配送方式后显示 end -->
			</div>
			<!-- 配送方式 end -->
			<div class="block pay_way">
				<mt-cell title="支付方式" value="货到付款" ></mt-cell>
				<p>目前只支持货到付款，您提交订单后，我们将货送到您手中再收费。</p>
			</div>
			<div class="block pay_way">
				<mt-field label="备注信息" v-model="remaks" placeholder="写下您的要求，让我们更好的为您服务。" type="textarea" rows="4"></mt-field>
			</div>
			<div class="block counts">
				<p class="d_flex"><span>商品总额：</span><span>{{ proCount }}</span></p>
				<p class="d_flex"><span>运费：</span><span>+ {{ defaultExp.byWeight | chargeFilter }}</span></p>
				<!-- 暂时不要促销价 
				<p class="d_flex"><span>促销立减：</span><span>- ￥5.00</span></p> 
				-->
				<div class="all_count d_flex">
					<span>订单总额：</span>
					<span class="r_color">{{ orderCount }}</span>
				</div>
			</div>
			<!-- 确认订单按钮 -->
			<div class="footer_btns d_flex">
				<p><span>实付金额: </span><span class="r_color">{{ orderCount }}</span></p>
				<button @click="comfireOrder">确认订单</button>
			</div>
			<!-- 确认订单按钮 end -->

			<!-- 选择配送方式 -->
			<mt-popup
			  class="sendWay"
			  v-model="popupSendWay"
			  position="bottom">
		  		<h3 class="d_flex"><span>配送方式</span><i @click="closeSendWay">X</i></h3>
		  		<ul>
		  			<li v-for="(sv,index) in sendValue" @click="selectSendWay(sv,index)">
		  				<label isradio>
		  					<input :checked=" sv.logisticsID == defaultExp.logisticsID " name="sendWay" type="radio"> 
	  						<i></i><span>{{ sv.logisticsName }}<i>运费{{ sv.byWeight | chargeFilter }}</i></span>
	  					</label>
		  			</li>
		  			<!-- <li>
		  				<label isradio>
		  					<input checked name="sendWay" type="radio"> 
	  						<i></i><span>圆通速递<i>运费￥10</i></span>
	  					</label>
		  			</li> -->
		  		</ul>
			</mt-popup>
			<!-- 选择配送方式 end -->
		</div>
	</section>
	
	<!-- 子页面 -->
    <transition name="fade">
    	<router-view v-on:changeClass="changeClassOne"></router-view>
    </transition>
    <!-- 子页面 end -->
</section>
</template>

<script>
import { Field, Header, Button, TabContainer, TabContainerItem, Cell, Popup, Radio, Toast, MessageBox } from 'mint-ui';

export default {
	name: 'Order',
	data (){
		return {
			flag: false,//
			selected: '1', //
			isUnscRollable: false, //是否页面不可滚动
			backParentPage: false, //是否回到父页面
			popupSendWay: false, // 是否显示配送方式
			sendValue: [], //配送方式
			params: this.$store.state.buyProductInfo,//产品ID orderId 数量 num 规格ID specId 规格名称 specName
			productData: '',//产品信息
			isDefaultAddress: false,//是否有默认地址
			defaultAddList: '',//默认地址信息
			defaultExp: '',//默认快递
			remaks: '',//客户信息
		}
	},
	components: {
		'mt-field': Field,
		'mt-header': Header,
		'mt-button': Button,
	    'mt-tab-container': TabContainer,
	    'mt-tab-container-item': TabContainerItem,
    	'mt-cell': Cell,
    	'mt-popup': Popup,
    	'mt-radio': Radio,
	},
	computed: {
		//商品总额
		proCount (){
			return '￥' + (this.productData.ourPrice * this.params.num).toFixed( 2 );
		},
		//订单总额
		orderCount (){
			if(!this.defaultExp.byWeight){
				return '￥' + (this.productData.ourPrice * this.params.num).toFixed( 2 );
			}else{
				return '￥' + (this.productData.ourPrice * this.params.num + this.defaultExp.byWeight.charge).toFixed( 2 );
			}
		},
		showAddress (){
			return this.isDefaultAddress;
		}
	},
	filters: {
		chargeFilter ( data ){
			if(!data || data.charge === 0){
				return '¥ 0.00';
			}else{
				return '¥ ' + data.charge;
			}
		},
	},
	updated (){
		// console.log('执行了更新')
	    // this.$nextTick(function () {
	    //   if( !this.backParentPage ){
	    //     this.isUnscRollable = true;
	    //   }
	    // }) 
		if( this.$store.state.buyProductAdd ){ //更新地址
			this.isDefaultAddress = true;
			this.defaultAddList = this.$store.state.buyProductAdd;
			// console.log('buyProductAdd和 ', this.$store.state.buyProductAdd)
		}
	},
	mounted (){
		this.$emit('changeClass',false);
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	created (){
		const _self = this;

		// console.log('选地址：', this.$store.state.buyProductAdd )
		//没有选择地址时使用默认地址
		if( !this.$store.state.buyProductAdd ){
			//获取地址
			this.$ajax({
				method: 'get',
				url: _self.webRoot + '/delivery/getDeliveryAddress?memberId='+_self.$store.state.memberId,
			})
			.then(function(res){
				// console.log('地址： ', res)
				if( res.data.length == 0 || res.data[0].isDefault == 0 ){
					//没有默认地址地址
					_self.isDefaultAddress = false;
				}else{
					//有默认地址地址
					_self.defaultAddList = res.data[0];
					_self.isDefaultAddress = true;
					
					//运费
					_self.$options.methods.getCharge( _self, res.data[0].addressID );
				}
			})
			.catch(function(err){
				console.log(err)
			});
		}else{
			//运费
			_self.$options.methods.getCharge( _self, _self.$store.state.buyProductAdd.addressID );
		}

		//获取商品信息
		this.$ajax({
			url: _self.webRoot + '/product/getProductByProductId',
			data: {
				productId: _self.params.orderId
			}
		})
		.then(function(res){
			// console.log(res)
			if( res.data.success ){
				if( !res.data.data ){
					_self.$options.methods.showToast(res.data.msg);
					return false;
				}
				_self.productData = res.data.data;
			}else{
				_self.$options.methods.showToast(res.data.msg);
			}
		})
		.catch(function(err){
			console.log(err)
		});

		//获取配送
		// this.$ajax({
		// 	url: _self.webRoot + '/require/getDefaultCarriage',
		// })
		// .then(function(res){
		// 	console.log('配送: ',res)
		// 	if( res.data.success ){
		// 		if( res.data.data.length == 0 ){
		// 			_self.$options.methods.showToast('无配送方式');
		// 			return false;
		// 		}
		// 		_self.sendValue = res.data.data;
		// 		for( var i = 0; i < res.data.data.length; i++ ){
		// 			if( res.data.data[i].logisticsID == 9 ){
		// 				_self.defaultExp = res.data.data[i];
		// 				break ;
		// 			}
		// 		}
		// 	}else{
		// 		_self.$options.methods.showToast(res.data.msg);
		// 	}
		// })
		// .catch(function(err){
		// 	console.log(err)
		// });

	},
	methods: {
		//确认订单 
		comfireOrder (){
			const _self = this;
			var address = this.defaultAddList.city;
			
			if( this.flag ){
				return false;
			}
			if( !address ){
				_self.$options.methods.showToast( '请选择收货地址!' );
				return false;
			}
			
			this.flag = true;
			MessageBox.confirm('确定执行此操作?', {closeOnClickModal: false}).then(action => {
				_self.$ajax({
					// url: "/apiserver/order/addorder",
					url: _self.webRoot + "/order/addorder",
					data: {
						productId: _self.params.orderId,//订单ID
						memberId: _self.$store.state.memberId,//会员ID
						detailCodeId: _self.params.specId,//多规格id
						qty: _self.params.num,//商品数量
						addressID: _self.defaultAddList.addressID,//地址编号
						deliveryAddress: address,//配送详细地址
						logisticsID: _self.defaultExp.logisticsID,//物流公司
						payType: 4,//支付方式 目前固定为 4 - 货到付款
						orderRemark: _self.remaks,//客户留言
						sysNo: 'WAP端',
					}
				})
				.then(function(res){
					// console.log(res)
					if( res.data.success ){
						gtag('event', '立即购买', {
						  'event_category' : _self.productData.productID,
						  'event_label' : '成功'
						});
						_self.$options.methods.showToast( res.data.msg );
						_self.$store.commit('addBuyProductInfo', '');//清除信息
						_self.$store.commit('addBbuyProductAdd', '');//清除信息
						_self.$router.go(-1);
					}else{
						gtag('event', '立即购买', {
						  'event_category' : _self.productData.productID,
						  'event_label' : '失败'
						});
						_self.$options.methods.showToast( res.data.msg );
					}
					_self.flag = false;
				})
				.catch(function(err){
					console.log(err)
				});
			}).catch(cancel => {
				console.log(cancel)
				_self.flag = false;
			});
		},
		//选择配送方式
		selectSendWay ( sv, index ){
			// console.log(sv, index)
			this.defaultExp = sv;
		},
	    // 点击显示选择配送方式
	    chooseSendWay (){
	    	this.popupSendWay = true; //显示
	    },
	    // 点击关闭选择配送方式
	    closeSendWay (){
	    	this.popupSendWay = false; //关闭
	    },
		// 后退
		goBack (){	
			this.$router.go(-1);
		},
		//确定按钮
		btnSave (){	
			console.log('点击确定')
		},
		//跳转选择地址
		jumpAddress (){
			this.$router.push({ path: '/myinfo/address?form=purch' });
		},

		//公共函数
		//获取运费
		getCharge ( _self, addressId ){
			//运费
			_self.$ajax({
				url: _self.webRoot + '/order/getLogisticsCharge',
				data: {
					addressId: addressId
				}
			})
			.then(function(res){
				// console.log('运费ss: ', res)
				if( res.data.length == 0 ){
					_self.$options.methods.showToast('无配送方式');
					return false;
				}
				_self.sendValue = res.data;
				for( var i = 0; i < res.data.length; i++ ){
					if( res.data[i].logisticsID == 9 ){
						_self.defaultExp = res.data[i];
						break ;
					}
				}
			})
			.catch(function(err){
				console.log(err)
			})
		},
	    //显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
	    // 判断是否返回父页面
	    changeClassOne ( isChildDestroyed ){
	      if( isChildDestroyed ){ 
	        //退出子页面
	        this.backParentPage = true; //返回首页
	        this.isUnscRollable = false;
	      }else{
	        //没退出子页面
	        this.backParentPage = false; // 没返回首页
	        this.isUnscRollable = true;
	      }
	    },
	},
}	
</script>

<style scoped>
@import '../../../static/css/headerTitle.css';
@import './Css/purchase.css';	
</style>


